import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cargo-time',
  templateUrl: './cargo-time.component.html',
  styleUrls: ['./cargo-time.component.scss'],
})
export class CargoTimeComponent implements OnInit {
  constructor(private router: Router) {}

  id: string = '0aFUX9x1r9bEDJnR4QNKfCD2QKp1';
  info: string;
  phone: number;
  car: number;

  selectedDate: string;
  selectedTime: string;

  ngOnInit(): void {
    let data = JSON.parse(localStorage.getItem('shipDetails'));
    console.log(data);
    if (data) {
      this.info = data.driver.info;
      this.car = data.driver.car;
      this.phone = data.driver.phone;
      this.selectedDate = data.date;
      this.selectedTime = data.time;
    } else {
      localStorage.removeItem('shipDetails');
    }
  }

  onDateChange(val) {
    this.selectedDate = val;
  }

  onTimeChange(val) {
    this.selectedTime = val;
  }

  watchOrder() {
    let shipDetails = {
      driver: {
        driverUid: this.id,
        info: this.info,
        phone: this.phone,
        car: this.car,
      },
      date: this.selectedDate,
      time: this.selectedTime,
    };
    console.log(shipDetails);
    localStorage.setItem('shipDetails', JSON.stringify(shipDetails));
    this.router.navigate(['dashboard/client/check-order']);
  }
}
