import { Component, OnInit } from '@angular/core';
import { Barcode } from 'src/app/models/barcode';
import { OrderService } from 'src/app/services/order.service';

@Component({
  selector: 'app-order-final-info',
  templateUrl: './order-final-info.component.html',
  styleUrls: ['./order-final-info.component.scss'],
})
export class OrderFinalInfoComponent implements OnInit {
  constructor(private orderService: OrderService) {}

  itemsToShip: Barcode[];
  shipDetails: any;
  driverInfo: [] = [];

  ngOnInit(): void {
    this.itemsToShip = JSON.parse(localStorage.getItem('itemsToShip'));
    this.shipDetails = JSON.parse(localStorage.getItem('shipDetails'));
    this.driverInfo = this.shipDetails.driver;
  }

  submitOrder() {
    let order = {
      items: this.itemsToShip,
      shipDetails: this.shipDetails,
      status: 'Новый',
      numStat: 0,
    };
    this.orderService.placeOrder(order);
    localStorage.removeItem('shipDetails');
    localStorage.removeItem('item');
    localStorage.removeItem('itemsToShip');
    localStorage.removeItem('items');
  }
}
