import { BarcodesService } from '../../../services/barcodes.service';
import { Component, OnInit } from '@angular/core';
import { Barcode } from 'src/app/models/barcode';
import { MatTableDataSource } from '@angular/material/table';
import { MatListOption } from '@angular/material/list';
import { Router } from '@angular/router';

@Component({
  selector: 'app-choose-barcodes',
  templateUrl: './choose-barcodes.component.html',
  styleUrls: ['./choose-barcodes.component.scss'],
})
export class ChooseBarcodesComponent implements OnInit {
  constructor(
    public barcodesService: BarcodesService,
    private router: Router
  ) {}

  codes: [];
  barcodeItems: Barcode[] = [];
  displayedColumns: string[] = [
    'ШК',
    'Номенклатура',
    'Склад',
    'Количество',
    'Ед.изм',
  ];
  dataSource: any;
  selectedOption: Barcode[];

  ngOnInit(): void {
    this.codes = JSON.parse(localStorage.getItem('items'));
    this.getBarcodes();
  }

  getBarcodes() {
    this.codes.forEach((el) => {
      let sub = this.barcodesService
        .getSelectedBarcodes(el)
        .subscribe((item) => {
          item.forEach((element) => {
            this.barcodeItems.push(element);
          });
          sub.unsubscribe();
          this.dataSource = new MatTableDataSource(item);
        });
    });
  }

  onGroupsChange(options: MatListOption[]) {
    this.selectedOption = options.map((o) => o.value);
    this.setSelectedItemsToLocalStorage();
  }

  setSelectedItemsToLocalStorage() {
    localStorage.setItem('itemsToShip', JSON.stringify(this.selectedOption));
  }

  chooseCargoTime() {
    this.router.navigate(['/dashboard/client/cargo-time']);
  }
}
