import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CargoTimeComponent } from './cargo-time/cargo-time.component';
import { ChooseBarcodesComponent } from './choose-barcodes/choose-barcodes.component';
import { ClientDashboardComponent } from './client-dashboard/client-dashboard.component';
import { ClientOrdersComponent } from './client-orders/client-orders.component';
import { OrderFinalInfoComponent } from './order-final-info/order-final-info.component';
import { OrderItemComponent } from './order-item/order-item.component';
import { PlaceOrderComponent } from './place-order/place-order.component';

const routes: Routes = [
  {
    path: '',
    component: ClientDashboardComponent,
  },
  {
    path: 'place-order',
    component: PlaceOrderComponent,
  },
  {
    path: 'choose-barcodes',
    component: ChooseBarcodesComponent,
  },
  {
    path: 'cargo-time',
    component: CargoTimeComponent,

  },
  {
    path: 'check-order',
    component: OrderFinalInfoComponent,

  },
  {
    path: 'orders',
    component: ClientOrdersComponent,
  },
  {
    path: 'orders/order/:id',
    component: OrderItemComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ClientRoutingModule {}
